html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
  /*El problema de hacer esto es que el padding también hace que la barra se aleje del top del viewport, 
  para eso...(ver paso 3 en _header.scss)*/
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background-color: #4CB8B3;
  z-index: 2;
}
@media (min-width: 768px) {
  .header {
    /*Paso 4 (Opcional) Para que la navbar se deslize hacia abajo al fjiarla, si no, lo hace de golpe 
    (Podemos quitarlo para ver como sería sin esto):
    Si no seteamos esto, la transición sería de 0 a 0, por eso la ponemos así*/
    top: -9.6rem;
    position: sticky;
    width: 100%;
    transition: top 0.3s;
  }
  .header.fijo {
    transition: top 0.3s;
    /*Paso 3- Con el padding se evita que el body se mueva de golpe, pero eso deja el header alejado...
    al fijar la posición arreglamos el problema*/
    /*Con esos pasos hicimos que el header al ponerse fijo después de llegar a cierto punto de scroll (navegacionFija() 
    en app.js, no moviera el body de golpe (podemos quitar lo de los 3 pasos para ver que pasa si no lo haciamos)*/
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.3rem 0.3rem 1.3rem #333;
    z-index: 2;
  }
}
.header h1 {
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
.header .contenido-header {
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    gap: 2rem;
  }
}
.navegacion-principal a {
  display: block;
  text-align: center;
  color: #FFFFFF;
  font-size: 2.4rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin: 0;
  }
}
.navegacion-principal a:hover {
  color: #FEF46D;
}

.video {
  position: relative;
  min-height: 50rem;
  max-height: 70rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  background: #a503ff;
  background: linear-gradient(270deg, rgba(165, 3, 255, 0.5) 0%, rgba(255, 0, 110, 0.3) 100%);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 992px) {
  .video video {
    width: 100%;
  }
}

.contenido-video {
  z-index: 1;
}
.contenido-video h2 {
  color: #FFFFFF;
  font-size: 7rem;
  margin: 0;
}
.contenido-video p {
  background-color: #F53756;
  display: inline-block;
  padding: 1rem 2rem;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 2.4rem;
  margin: 0;
}

.sobre-festival {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.contenido-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .contenido-festival {
    margin-top: 0;
  }
}
.contenido-festival h2 {
  margin-bottom: 2rem;
}
.contenido-festival .fecha {
  color: #F53756;
  font-weight: 700;
}

.lineup {
  background-color: #F53756;
  padding: 5rem 0;
}
.lineup h3, .lineup p {
  text-align: center;
  color: #FFFFFF;
}
.lineup .dia {
  text-transform: uppercase;
  margin: 5rem 0;
}

@media (min-width: 768px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.escenarios-contenedor p {
  color: #000000;
}

.escenario {
  padding-top: 2rem;
}
.escenario .nombre-escenario {
  /*Cada parrafo (.nombre-escenario) de .escenario (div) tendrá justo después (por eso el :after) un background seteado para poner el 
  ícono de audifonos o guitarra (sin el ícono quedaría un campo vacío con estas specs)*/
}
.escenario .nombre-escenario:after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 0 auto;
  margin: 2rem auto 0 auto;
}

/*Ya seteado el background para el ícono, 
ponemos el ícono en después (:after) de cada parrafo (.nombre-escenario) de cada div .edm*/
.edm .nombre-escenario:after {
  background-image: url(../img/icono_edm.png);
}

.rock .nombre-escenario:after {
  background-image: url(../img/icono_rock.png);
}

.calendario {
  margin: 0;
  padding: 0;
  list-style: none;
}
.calendario li {
  padding: 2.5rem;
  font-weight: 700;
}
.calendario li span {
  font-weight: 400;
}

.bg-amarillo {
  background-color: #FEF46D;
}
.bg-amarillo li:nth-child(odd) {
  background-color: #fdee21;
}

.bg-verde {
  background-color: #4CB8B3;
}
.bg-verde li:nth-child(odd) {
  background-color: #3b9691;
}

.galeria {
  padding: 5rem 0;
}
.galeria h3 {
  text-align: center;
}

.galeria-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.imagen-cursor:hover {
  cursor: pointer;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.btn-cerrar {
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  background-color: #FFFFFF;
  color: #000000;
  font-weight: 900;
  display: grid;
  place-items: center;
}
.btn-cerrar:hover {
  cursor: pointer;
}

.boletos {
  padding: 5rem 0;
}
.boletos h3 {
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .boletos .contenedor-boletos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pase .nombre-pase {
  text-align: center;
  font-size: 2.8rem;
  color: #FFFFFF;
}
.pase-header, .pase-footer {
  padding: 0.5rem;
}
.pase-header p, .pase-footer p {
  text-align: center;
  color: #FFFFFF;
  margin: 0;
}
.pase-header {
  background-color: #F53756;
}
.pase-header p {
  text-transform: uppercase;
  font-size: 1.6rem;
}
.pase-body {
  background-color: #FFFFFF;
}
.pase-body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.pase-body ul li, .pase-body ul :nth-child(odd) {
  background-color: #FFFFFF;
  padding: 1rem;
  border-bottom: 1px solid #e1e1e1;
}
.pase-footer {
  background-color: #752F97;
}
.pase-footer p {
  font-size: 2.4rem;
  font-weight: 700;
}

.site-footer {
  background-color: #F53756;
  padding: 1rem;
}
.site-footer p {
  margin: 0;
  text-align: center;
  color: #FFFFFF;
}

/*podemos ejecutar sass con scripts del package.json usando los bin, en este caso, sass lo tiene pero no siempre será así
ej:
Lee sass en la carpeta donde está tu archivo (no necesitamos poner archivo)

nombre del script:el bin

"scripts": {
    "sass":"sass --watch src/scss:build/css"
  }
  Resumen: Abre el bin de Sass, lo lee en la 1er carpeta (no necesitamos poner el file, solo el path)
  y almacena la versión compilada en la 2da ya que necesitamos que el archivo final sea CSS
  --watch va a escuchar cada cambio y va a compilar automaticamente

  Compilando SASS:
  --Con npm
  npm run sass (el nombre del script)

  --con gulp
  creamos un gulpfile y lo trabajamos:
  gulp tarea (tarea es función de JS)


y el index debe apuntar al css compilado, no al sass
*/
/*Los archivos con "_" al inicio, SASS los reconoce como archivos secundarios que no crearán otras hojas de estilos 
y por eso los debemos importar en el principal (en el que no tiene "_" al inicio del nombre)*/